.App {
  text-align: center;
  min-height: 110vh;
}
.navbar {
  min-height: 80px;
  margin-top: -20px;
  overflow: hidden;
}
.navbar-brand {
  color: white;
  padding-top: 20px;
  font-size: 30px;
  font-weight: 500;
}
.img-fluid {
  height: 30rem!important;
    width: 100%!important;
}
.form-inline {
  margin-top: 17px !important;
}
.imgBox {
  width: 100px;
  height: 100px;
  background-size: cover;
  border: 1px solid #3F729B;
  margin-top: 10px;
}
iframe{
  border:none;
}

img{
  object-fit: cover;
}

body{
  overflow-x: hidden;
}

@media only screen and (max-width:766px){
  #footer{
    display:none;
  }
  #logo{
    font-size:33px !important;
  }
}


@media only screen and (max-width: 600px) {
  .hideOnMobile {
   display:none;
  }
  
  .btn.btn-outline-white.btn-md.my-2.my-sm-0.ml-3{
    min-width: 147px !important;
  }
}

.col-lg-4.col-md-12.mb-lg-0.mb-0 > div{
  border-radius:15px !important;
}

p > a{
  color:white;
  font-weight: bold;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
}

#logo{
  font-family: 'Cinzel' !important;
  font-size:42px;
}

.h1-responsive{
  font-family: "Zen Dots";
  font-weight: 400;
    text-overflow: ellipsis;
  font-size:40px;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black !important;
  color:white !important;
  margin-left: 5vw;
  margin-right: 5vw;
  overflow:hidden;
}

.h1-responsive.normal{
  font-size:36px;
  mix-blend-mode: unset !important;
}

.h1-responsive > .detail{
  font-family: "Roboto", sans-serif !important;
}

.stylish-color{
  background-color: #0a0a0a77!important;
  border-bottom: 1px solid #ffffff59;
}

.customA {
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black !important;
  color:white !important;
}

.customA:hover {
  color:white !important;
}

.btn-red{
  background-color:#ff0000!important;
}

.marginTopC{
  margin-top:15px;
}

.text20{
  font-size:20px !important;
}


.bg-button-green{
  background-color: #388e3c !important;
}

@media (min-width: 1200px){
.container {
    max-width: 1200px;
}

#logo{
  font-size:50px;
}
iframe{
  height:700px !important;
}
}

@media (min-width: 1350px){
  .container {
      max-width: 1350px;
  }
}

@media (min-width: 1450px){
  .container {
      max-width: 1450px;
  }
}